@import url(https://fonts.googleapis.com/css?family=Lato:100,100italic,300,300italic,regular,italic,700,700italic,900,900italic&display=swap);
:root {
  /* Color Palette */
  --white: #f4f4f4;
  --gray: #646464;
  --gray-csv: 212, 212, 212;
  --bg-green: #152010;
  --bg-blue: #041424;
  --bg-violet: #160824;
  --bg-dark: #2a2a2a;
  --problem: hsl(0, 50%, 83%);
  --solution: hsl(120, 50%, 83%);
}

body {
  margin: 0;
  font-family: Lato, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #f4f4f4;
  color: var(--white);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Portfolio {
  background: radial-gradient(#2a2a2a, black);
  background: radial-gradient(var(--bg-dark), black);
}
.Techs {
  background: radial-gradient(#160824, black);
  background: radial-gradient(var(--bg-violet), black);
}
.Contact {
  background: radial-gradient(#152010, black);
  background: radial-gradient(var(--bg-green), black);
}

section{
  min-height: 100vh;
  width: 100%;
  text-align: center;
}
.landing-wrapper {
    background-size: cover;
    background-position: left;
}

.landing-wrapper > h1 {
    position: absolute;
    top: 47%;
    left: 28%;
    font-size: 2.25rem;
}
.landing-wrapper > h2 {
    position: absolute;
    top: 59%;
    left: 34%;
    font-size: 1.25rem;
}
.Story {
    background: radial-gradient(var(--bg-blue), black);
    
}

.story-grid {
    display: grid;
    place-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    grid-template-columns: 5fr 1fr 5fr;
}

.problem, .solution {
    width: 100%;
    max-width: 20rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: .9rem;
    font-weight: bold;
    padding: 1rem;
    display: grid;
    place-items: center;
    color: var(--bg-dark);
    border-radius: 1rem;
}

.icon {
    font-size: 3rem;
}
.problem {
    background-color: var(--problem);
    margin-left: auto;
}
.solution {
    background-color: var(--solution);
    margin-right: auto;
}

@media (max-width: 768px) {
    .problem, .solution {
        font-size: .8rem;
    }
}
.project-preview {
    position: relative;
    background-size: cover;
    background-position: top center;
    width: 100%;
    height: 100%;
    min-height: 15rem;
    transition: all 300ms ease-in;
    color: var(--bg-dark);
}
.project-preview:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    z-index: 1;
    box-shadow: 2px 2px 5px;
}

.overlay {
    display: grid;
    place-items: center;
    position: absolute;
    top:0; left:0;
    background-color: rgb(var(--gray-csv), 0.6);
    width: inherit;
    height: inherit;
    padding: .5rem;
    font-size: 1.5rem;
    font-weight: bold;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.overlay:hover {
    -webkit-animation: fadeout 300ms forwards;
            animation: fadeout 300ms forwards;
}
  
@-webkit-keyframes fadeout {
    to {        
        opacity: 0;
        cursor: pointer;
        -webkit-user-select: none;
                user-select: none;
    }
}
  
@keyframes fadeout {
    to {        
        opacity: 0;
        cursor: pointer;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }
}
.wrapper {
    display: grid;
    place-items: center;
}

.Techs .btn {
    height: 5rem;
    pointer-events: none;
    max-width: 25vw;
}

.Techs .mern::first-letter {
    font-size: larger;
    font-weight: bolder;
}
.nav-link {
    font-weight: 600;
}

.navbar-collapse{
    position: absolute;
    right: 0;
    top:0;
    z-index: 1;
    min-height: 100%;
}
.navbar-collapse.show, .navbar-collapse.collapsing { 
    top:100%;
    background-color: rgb(var(--gray-csv), 0.8);
    border-bottom-left-radius: 100%;
}
.footer {
    position: relative;
    background-color: var(--gray);
}

.footer a {
    color: var(--white);
    -webkit-text-decoration-color: var(--white);
            text-decoration-color: var(--white);
}

#coffee {
    height: 2.25rem;
    position:absolute;
    left: 1rem;
    bottom: 1rem;
}

#logos img {
    position:relative;
    margin-left: 1rem;
    margin-right: 1rem;
}

@media (max-width: 768px) {
    #coffee {
        height: 1.75rem;
        left: unset;
        right: .5rem;
        bottom: .5rem;
    }
    
    #logos img {
        width: 1.75rem;
        margin-left: .5rem;
        margin-right: .5rem;
    }
}
